.about{
    padding-bottom: 48px;
}
.about-top{
    display: flex;
    justify-content: space-between;
    align-items: center;
}
.picture{
    height: 420px;
}
.about-info{
    font-size: 24px;
}
.name-info{
    color: blueviolet;
    font-size: 32px;
}
@media only screen and (max-width:720px) {
    .about-top{
        flex-direction: column;
        justify-content: center;
        align-items: center;
    }
    .picture{
        height: 250px;
    }
    .about-info{
        font-size: 20px;
    }
    .name-info{
        font-size: 26px;
    }
}